import React, {useEffect, useState} from 'react';
import {Container, Row, Col} from "react-bootstrap";
import Layout from "../components/layout";

const Contacto = () => {
	const [scroll, setScroll] = useState(0);
	
	
	const listenToScroll = () => {
		const winScroll =
			document.body.scrollTop || document.documentElement.scrollTop
		
		const height =
			document.documentElement.scrollHeight -
			document.documentElement.clientHeight
		
		const scrolled = winScroll / height
		
		setScroll(scrolled)
	};
	
	useEffect(() => {
		if (typeof window !== `undefined`) {
			window.addEventListener('scroll', listenToScroll);
		}
		
		return () => window.removeEventListener('scroll', listenToScroll)
		
	},[]);
	
	useEffect(() => {
		console.log('scroll', scroll)
		
	},[scroll]);
	
	return (
			<div>
			<Container>
				<Row>
					<Col style={{padding: '7%'}}>
						<p style={{
							fontSize: '1.5rem',
							fontWeight: 'bold',
							color: `rgb(${scroll * 50 + 200}, ${scroll * 20 + 0}, ${scroll * 200}`,
							fontFamily: 'Business'
						}}>
							Contacto:<br/>
							<a style={{
							fontSize: '1.5rem',
							fontWeight: 'bold',
							color: `rgb(${scroll * 50 + 200}, ${scroll * 20 + 0}, ${scroll * 200}`,
							fontFamily: 'Business'
						}} href="mailto:yerbamalazine@gmail.com">yerbamalazine@gmail.com</a><br/>
							<a style={{
							fontSize: '1.5rem',
							fontWeight: 'bold',
							color: `rgb(${scroll * 50 + 200}, ${scroll * 20 + 0}, ${scroll * 200}`,
							fontFamily: 'Business'
						}} href="https://instagram.com/yerbamalazine">instagram.com/yerbamalazine</a><br/>
							<a style={{
							fontSize: '1.5rem',
							fontWeight: 'bold',
							color: `rgb(${scroll * 50 + 200}, ${scroll * 20 + 0}, ${scroll * 200}`,
							fontFamily: 'Business'
						}} href="https://instagram.com/nedtemp">instagram.com/nedtemp</a>
						</p>
					</Col>
				</Row>
			</Container>
			</div>
	);
};

export default Contacto;
